//车辆查询
export function vehicleListQuery(params){
  return fetch({
    url:"/vehicleInfo/vehicleList",
    method:'post',
    data:params,
    headers:{
      "Content-Type":"application/json"
    }
  })
}
//过车数据查询
export function AllVehicleList(params){
  return fetch({
    url:"/AllVehicle/list",
    method:'post',
    data:params,
    headers:{
      "Content-Type":"application/json"
    }
  })
}
//baaner数据
export function bannerMsg(){
  return fetch({
    url:"/index/getMenu",
    method:'get',
  })
}


